<template>
  <div>
    <el-container>

      <!-- 侧边栏 -->
      <el-aside :width="asideWidth" style="min-height: 100vh;background-color: #001529">
        <div style="height: 60px ;color: white; display: flex; align-items: center; justify-content: center">
          <img src="@/assets/logo.png" alt="" style="width: 40px; height: 40px">
          <span class="logo-title" style="" v-show="!isCollapse">校园安防</span>
        </div>
        <el-menu :collapse="isCollapse" :collapse-transition="false" router background-color="#001529" text-color="rgba(255,255,255,0.65)"
                active-text-color="#fff" style="border: none" :default-active="$route.path">   <!-- :default-active="$route.path 是当前路由高亮-->
          <el-menu-item index="/admin/home" style="justify-content: center; align-content: center">
            <i class="el-icon-s-home" style=""></i>
            <span slot="title" style="">系统首页</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>

        <!-- 头部区域 -->
        <el-header>
          <i :class="collapseIcon" style="font-size: 26px"  @click="handleCollapse"></i>
          <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px">
            <el-breadcrumb-item :to="{ path: '/admin/home' }">主页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: $route.path }">{{ $route.meta.name }}</el-breadcrumb-item>
          </el-breadcrumb>
          <div style="flex: 1; width: 0; display: flex; align-items: center; justify-content: flex-end">
            <i class="el-icon-quanping" style="font-size: 20px" @click="handleFull"></i>
            <el-dropdown placement="bottom">
              <div style="display: flex; align-items: center; cursor: default; margin-left: 20px">
                <img :src="user.avatar" alt="" style="width: 40px; height: 40px; margin:0 5px; border-radius: 50%">
                <span>{{ user.name }}</span>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="$router.push('/admin/person')">个人信息</el-dropdown-item>
                <el-dropdown-item @click.native="$router.push('/admin/password')">修改密码</el-dropdown-item>
                <el-dropdown-item><div @click="logout">退出登录</div></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>

        <!-- 主题区域 -->
        <el-main>
          <!-- 将路由展示在主体区 -->
          <router-view @update:user="updateUser"/>
        </el-main>
      </el-container>
    </el-container>


  </div>

</template>

<script>

import request from "@/utils/request";

export default {
  name: 'HomeView',
  data() {
    return {
      isCollapse: false,    /*不收缩*/
      asideWidth: '200px',
      collapseIcon: 'el-icon-s-fold',
      user: JSON.parse(localStorage.getItem('honey-admin') || '{}'),
    }
  },
  mounted() {   // 页面加载完成后触发
  },
  methods: {
    // 获取子组件传过来的数据，更新当前页面的数据
    updateUser(user) {
      this.user = JSON.parse(JSON.stringify(user))  // 让父级的对象和子集的对象毫无关联
    },
    preview(url) {
      window.open(url)  // 默认图片是预览的
    },
    showUrls() {
      console.log(this.urls)
    },
    handleMultipleFileUpload(response, file, fileList) {
      // console.log(response, file, fileList)
       this.urls = fileList.map(v => v.response?.data)
    },
    handleTableFileUpload(row, file, fileList) {
      console.log(row, file, fileList)
      row.avatar = file.response.data
      // this.$set(row, 'avatar', res.data)
      console.log(row)
      // 触发更新
      request.put('/user/update', row).then(res => {
        if (res.code === '200') {
          this.$message.success('上传成功')
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    handleFileUpload(response, file, fileList) {
      this.fileList = fileList
    },
    logout() {
      localStorage.removeItem("honey-admin")     // 清除当前的token和用户数据
      this.$router.push("/login")
    },
    handleCollapse() {
      this.isCollapse=!this.isCollapse
      this.asideWidth = this.isCollapse ? '64px' : '200px'
      this.collapseIcon = this.isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'
    },
    handleFull() {
      document.documentElement.requestFullscreen()
    }
  }
}
</script>

